html, body {
  width: 100%;
  height: 100%;
  background-color: var(--primaryColor);
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

.container {
  max-width: 1536px;
  width: 100%;
  height: fit-content;
  margin: 0 20px;
  margin: auto;
}

@media only screen and (max-width: 1636px) {
  .container {
    max-width: 936px;
    width: 100%;
    height: fit-content;
    margin: 0 20px;
    margin: auto;
  }
}

:root {
  --primaryColor: rgb(24, 24, 24);
  --secondaryColor: rgb(32, 32, 32);
}