header {
    width: 100%;
    height: 80px;
    background-color: var(--secondaryColor);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
}

.navbar {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    height: 100%;
    align-items: center;
    width: 100%;
}

.navbar > .logo {
    padding: 20px;
    margin: 0;
    height: 64px;
    width: 64px;
    color: white;
    font-size: 26px;
}

.navbar-items {
    display: flex;
    flex-direction: row;
    list-style-type: none;;
}

.navbar-items > .navbar-item {
    margin: 0 20px;
    border-radius: 8px;
}

.navbar-item > a {
    color: white;
    text-decoration: none;
    padding: 20px 30px;
    transition: 0.5s;
}

.navbar-item > a:hover {
    background-color: var(--primaryColor);
}

.language-selector {
    display: flex;
    flex-direction: row;
}

.language-selector > a {
    color: white;
    text-decoration: none;
    padding: 20px 40px;
    margin: 0 20px 0 0;
    transition: 0.5s;
    background-color: var(--primaryColor);
    border-radius: 8px;
}

.language-selector > button {
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.navbar-item-holder {
    display: flex;
    flex-direction: row;
}

.navbar-toggler {
    visibility: hidden;
}

@media screen and (max-width: 768px) {
    .navbar-toggler {
        visibility: visible;
    }

    .navbar-item-holder {
        position: fixed;
        top: 80px; /* Adjust according to your header height */
        left: 0;
        width: 100%; /* Ensure it takes full width */
        display: flex;
        flex-direction: column;
        background-color: var(--primaryColor);
        height: calc(100vh - 80px); /* Adjust height considering header height */
        transition: transform 0.3s ease-in-out; /* Add transition effect */
        transform: translateX(-100%); /* Initially hide off-screen */
    }

    .navbar-items {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .navbar-items > .navbar-item {
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: center;
        margin: 0;
    }

    .navbar-item > a {
        text-align: center;
        width: 100%;
    }

    .navbar-item > a:hover {
        background-color: var(--secondaryColor);
    }

    .navbar-item-holder.open {
        transform: translateX(0); /* Show navbar when open */
    }

    .language-selector {
        justify-content: center;
    }
}