.projects-holder {
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.projects-holder >.projects-title {
    color: white;
}

.projects-card {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.projects-card > .project {
    width: 100%;
    height: 250px;
    display: flex;
    flex-direction: row;
    border-radius: 16px;
    background-color: var(--secondaryColor);
    margin: 50px 0;
}

.projects-card > .project.right {
    direction: rtl;
}

.projects-card > .project > img {
    width: fit-content;
    height: 100%;
    margin: 0 50px 0 0;
}

.projects-card > .project > .information {
    display: flex;
    flex-direction: column;
    padding: 30px;
}

.projects-card > .project > .information > .title {
    margin: 10px 0;
    color: rgb(0, 175, 255);
    font-size: 22px;
}

.projects-card > .project > .information > .description {
    margin: 10px 0;
    color: white;
    font-size: 18px;
}

@media screen and (max-width: 768px) {
    .project {
        flex-direction: column!important;
        height: fit-content!important;
        align-items: center;
    }
}