.about-page {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.profile-card {
    width: 30%;
    padding: 30px;
    border-radius: 16px;
    background-color: var(--secondaryColor);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: fit-content;
    background: linear-gradient(45deg, rgb(44, 44, 44) 0% 0%, hsla(0, 0%, 0%, 1) 100%);
}

.profile-card  > img {
    border-radius: 8px;
    width: 100%;
    height: fit-content;
}

.profile-card > .fullName {
    color: white;
    font-size: 26px;
}

.profile-card > .user-handle {
    color: gray;
    font-size: 18px;
    margin: 0 0 20px 0;
}

.profile-card > .social-media {
    display: flex;
    flex-direction: row;
    margin: 20px 0;
}

.profile-card > .social-media > a {
    margin: 0 20px;
}

.profile-card > .social-media > a > svg {
    color: gray;
    font-size: 36px;
}

.profile-card > a {
    color: white;
    text-decoration: none;
    font-size: 22px;
    margin: 20px 0;
    background-color: var(--primaryColor);
    border-radius: 8px;
    padding: 10px 80px;
}

.profile-description {
    width: 60%;
    background-color: var(--secondaryColor);
    border-radius: 16px;
    padding: 30px;
    display: flex;
    flex-direction: column;
}

.profile-description-block {
    display: flex;
    flex-direction: column;
}

.profile-description-title {
    color: white;
    font-size: 26px;
    width: 100%;
    border-bottom: 3px solid var(--primaryColor);
    padding: 10px 0;
}

.profile-description-title > span {
    background-color: var(--primaryColor);
    padding: 10px 20px;
    border-top-right-radius: 16px;
}

.profile-description-year {
    color: gray;
    margin: 0;
}

.profile-description-job-name {
    margin: 10px 0;
    color: rgb(0, 175, 255);
    font-size: 22px;
}

.profile-description-buisness-name {
    margin: 0;
    color: gray;
}

.profile-description-text {
    color: white;
    font-size: 18px;
}

.skill-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.skill-holder {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    padding: 20px;
}

.skill-holder > p {
    color: white;
    font-size: 22px;
}

.skill-holder > .skill {
    display: flex;
    flex-direction: column;
    align-items: start;
    width: 100%;
    margin: 20px 0;
}

.skill-holder > .skill > .skill-name {
    color: white;
    padding: 0;
    font-size: 16px;
    margin: 0 0 10px 20px;
}

.skill-holder > .skill > .skill-bar {
    width: 100%;
    height: 30px;
    border-radius: 60px;
    background-color: var(--primaryColor);
    overflow: hidden;
}

.skill-holder > .skill > .skill-bar > .skill-bar-fill {
    background-color: white;
    height: 100%;
}

@media only screen and  (max-width: 1636px) {
    .skill-list {
        grid-template-columns: 1fr;
    }
}

@media only screen and (max-width: 1036px) {
    .about-page {
         display: flex;
         flex-direction: column;
    }

    .profile-card {
        width: 100%;
        margin-bottom: 20px;
    }

    .profile-description {
        width: 100%;
    }

    .skill-list {
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (max-width: 636px) {
    .skill-list {
        grid-template-columns: 1fr ;
    }
}